	import Vue from 'vue'
	import moment from 'moment'
	import util from '@/libs/util'
	import {_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import XTable from 'c%/table'
	import XPage from 'c%/page'
	
	export default{
		mixins: [ common,globals ],
		components: {
			XTable,
			XPage
		},
		props: {
			bids: {
				type: Array,
				default: []
			},
			group: {
				type: String,
				default: 'bid'
			},
			yearMonth: {
				type: String,
				default: moment(new Date()).format('YYYY-MM')
			}
		},
		data () {
			return {
				data: [],
				total: 0,
				pageIndex:1,
				pageSize:10
			}
		},
		watch: {
			group: function (val) {
				this.init()
			},
			yearMonth: function (val) {
				this.init()
			},
			bids: function (val) {
				this.init()
			},
		},
		mounted () {
			this.init_data()
		},
		methods: {
			init () {
				this.pageIndex = 1
				this.init_data()
			},
			pagenation (page) {
				this.pageIndex = page            
            	this.init_data()     
			},
			init_data () {
				let params = {
					page: this.pageIndex,
					pagesize: this.pageSize
				}
				this.showLoading()
				this.$rest(this.data_uri).get(params)
				.success(data=>{
					this.data = data.list
					this.total = data.total
                	this.pageSize = parseInt(data.pagesize)
					this.loadFinished()
				})
			},
			loadFinished () {
				let data = this.data
				
				if(data&&data.length>0){
					data.push({
						column_total:true
					})
				}
				
				setTimeout(()=>{
					this.hideLoading()
				},300)
			},
			//获取列统计
			getTotal(h,field) {
				let data = this.data,
					result = 0
				if(data&&data.length>0){
					data.forEach(d=>{
						if(d[field]){							
							result += Number(d[field])							
						}
					})
					
					if(!_.isNumber(data[data.length-2][field])){
						result = parseFloat(result).toFixed(2)	
					}
				}
				return h('span',{'class':'text-danger'},result)
			}
		},
		computed: {
			tableHeight () {
				let winHeight = window.innerHeight||document.innerHeight
				return winHeight - (46 + 55 + this.headerHeight) 
			},
			columns () {
				let columns = []
				let first_column = {}
				if(this.group=='bid'){
					first_column = {
						title: '校区',
						key: 'bid',
						width: 100,
                        fixed: 'left',
                        render: (h,params)=>{
                        	if(params.row.column_total){
                        		return '合计'
                        	}
                        	return h('span',this.$filter('branch_name')(params.row.bid))
                        }
					}
				}else if(this.group=='lid'){
					first_column = {
						title: '课程',
						key: 'lid',
						width: 100,
                        fixed: 'left',
						render: (h,params)=>{ 
							if(params.row.column_total){
                        		return '合计'
                        	}
                        	return h('span',this.$filter('lesson_name')(params.row.lid))
                        }
					}
				}else if(this.group=='sj_id'){
					first_column = {
						title: '科目',
						key: 'sj_id',
						width: 100,
                        fixed: 'left',
						render: (h,params)=>{   
							if(params.row.column_total){
                        		return '合计'
                        	}                     	
                        	return h('span',this.$filter('subject_name')(params.row.sj_id))
                        }
					}
				}else if(this.group=='cid'){
					first_column = {
						title: '班级',
						key: 'cid',
						width: 100,
                        fixed: 'left',
						render: (h,params)=>{
							let result = '未分班'
							if(params.row.column_total){
                        		return '合计'
                        	}
							if(params.row.cid>0&&params.row.cls){
								result = params.row.cls.class_name 
							}
                        	return h('span',result)
                        }
					}
				}else if(this.group=='eid'){
					first_column = {
						title: '老师',
						key: 'eid',
						width: 100,
                        fixed: 'left',
						render: (h,params)=>{
							if(params.row.column_total){
                        		return '合计'
                        	}
							return h('span',this.$filter('ename')(params.row.eid))
						}
					}
				}else if(this.group=='sid'){
					first_column = {
						title: '学员',
						key: 'sid',
						width: 100,
                        fixed: 'left',
						render: (h,params)=>{
							let result = ''
							if(params.row.column_total){
                        		return '合计'
                        	}
							if(params.row.student){
								result = params.row.student.student_name
							}
							return h('span',result)
						}
					}
				}
				let column_sum_student = {
						title: '学员数',
						key: 'sum_student_nums',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return this.getTotal(h,'sum_student_nums')
							}
							return params.row.sum_student_nums
						}
					},					
					column_sum_lesson_hours = {
						title: '课时数',
						key: 'sum_lesson_hours',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return this.getTotal(h,'sum_lesson_hours')
							}
							return params.row.sum_lesson_hours
						}
					},
					column_sum_lesson_times = {
						title: '课次数',
						key: 'sum_lesson_times',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return this.getTotal(h,'sum_lesson_times')
							}
							return params.row.sum_lesson_times
						}
					},
					column_sum_total_lesson = {
						title: '总课时数',
						key: 'sum_total_lesson_hours',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return this.getTotal(h,'sum_total_lesson_hours')
							}
							return params.row.sum_total_lesson_hours
						}
					},
					column_sum_total_lesson_amount = {
						title: '总金额',
						key: 'sum_total_lesson_amount',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return this.getTotal(h,'sum_total_lesson_amount')
							}
							return params.row.sum_total_lesson_amount
						}
					},
					column_sno = {
						title: '学号',
						key: 'sno',
						width: 100,
						render: (h,params)=>{
							if(params.row.student){
								return h('span',params.row.student.sno)
							}
							return ''
						}
					}
					
				columns.push(first_column)
				if(this.group != 'sid'){
					columns.push(column_sum_lesson_hours,column_sum_lesson_times,column_sum_student,column_sum_total_lesson)
				}else{
					columns.push(column_sno,column_sum_lesson_hours,column_sum_lesson_times)
				}
				columns.push(column_sum_total_lesson_amount)
				return columns
			},
			data_uri () {
				let group = this.group,
					start_date = this.yearMonth+'-01',
					end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
					base_uri = 'report_employee_outputs',
					uri = '',
					bids = ''

				if(this.bids.length){
					bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
				}
				if(group == 'sid'){
					base_uri = 'report_student_consumes'
				}
					
				uri = util.sprintf('%s?group=%s&start_date=%s&end_date=%s%s',base_uri,group,start_date,end_date,bids)
					
				return uri
			}
		}
	}



