return {
popupShow: false,
filter: ['bid'],
filterList:[
{value: 'bid',name:'按校区'},
{value: 'lid',name:'按课程'},
{value: 'sj_id',name:'按科目'},
{value: 'cid',name:'按班级'},
{value: 'eid',name:'按老师'},
{value: 'sid',name:'按学员'}
]
}
}
}




